export const setTasksTest = () => {
      let tasks = [];
      for (let index = 0; index <= 50; index++) {
            let status = ""
            let number = index + 1;
            if (number == 1) {
                  status = " "
            }
            if (number == 2) {
                  status = " "
            }
            if (number % 3 == 0 && number % 2 != 0) {
                  status = " "
            }
            if (number % 4 == 0) {
                  status = " "
            }
            if (number % 5 == 0) {
                  status = ""
            }



            let task = {
                  id: index + 1,
                  date: "",
                  type: "",
                  name: " ",
                  descrShort: "   ",
                  category: "",
                  descrDetailed: "",
                  address: " ",
                  telephone: "89298777777",
                  startDate: "Sat Jan 18 2020 16:08:00 GMT+0300 (,  )",
                  endDate: "Sat Jan 28 2020 19:00:00 GMT+0300 (,  )",
                  startAddress: " , ,3",
                  endAddress: " ,   11",
                  taskFlowShort: "   ,  ",
                  taskFlowDetailed: ["  ", "  ", "  ", " "],
                  currentStatus: status
            };

            tasks.push(task)
      }
      console.log(tasks)
      return tasks
}
export const getTasksTest = (page) => {

      let tasks = setTasksTest();
      let fetchedTasks = []
      for (let i = page * 10 - 10; i < page * 10; i++) {
            fetchedTasks.push(tasks[i]);
      }
      console.log(fetchedTasks)
      return fetchedTasks
}